<template>
  <div class="auth">
    <div class="authing" v-if="$yin.me._token&&!$yin.me._id">
      受权中...
    </div>
    <n-card title="引.object - 对象操作台" class="table" v-else>
      <template v-if="$yin.system.uninitialized">
        <n-h4>初始化</n-h4>
        <p>此时注册的用户将为管理员</p>
      </template>
      <n-tabs default-value="password" size="large">
        <n-tab-pane name="password" tab="密码">
          <n-form-item-row label="手机号">
            <n-input v-model:value="_tel" type="tel" placeholder="请输入手机号"/>
          </n-form-item-row>
          <n-form-item-row label="密码" :feedback='msg'>
            <n-input v-model:value="_password" type="password" center clearable placeholder="请输入密码"/>
          </n-form-item-row>
          <n-form-item-row label="确认密码" v-if="register">
            <n-input v-model:value="confirmPassword" type="password" center clearable
                     placeholder="再次输入密码"/>
          </n-form-item-row>

          <div class="btn raw" v-if="!register">
            <n-button @click="authPassword" block type="primary">登录</n-button>
            <n-button @click="register=true" block type="default">注册</n-button>
          </div>
          <div class="btn raw" v-if="register">
            <n-button @click="register=false" block type="default">登录</n-button>
            <n-button @click="reg" block type="primary">注册</n-button>
          </div>
        </n-tab-pane>
        <n-tab-pane name="code" tab="验证码">
          <p class="title">输入手机号自动登录/注册</p>

          <n-form-item-row label="手机号">
            <n-input v-model:value="_tel" type="tel" placeholder="请输入手机号"/>
          </n-form-item-row>
          <n-form-item-row label="短信验证码" :feedback='msg'>
            <n-input-group>
              <n-input v-model:value="code" center clearable placeholder="请输入短信验证码"
                       autocomplete="one-time-code"
              />
              <n-button :disabled="_tel===''" type="primary" v-if="!device" @click="getCode">
                发送验证码
              </n-button>
              <n-button :disabled="true" type="primary" v-if="device&&time>0">
                已发送({{ time }})
              </n-button>
              <n-button type="primary" v-if="device&&time===0" @click="getCode">
                再次发送
              </n-button>
            </n-input-group>

          </n-form-item-row>
          <div class="btn raw">

            <n-button type="primary" block @click="auth">登录/注册</n-button>
          </div>
        </n-tab-pane>
      </n-tabs>
    </n-card>
  </div>

</template>

<script lang="ts">
import {defineComponent} from 'vue';
import {useNotification, NCard, NTabs, NTabPane, NButton, NFormItemRow, NInput, NInputGroup} from 'naive-ui'

export default defineComponent({
  name: 'AuthView',
  components: {NCard, NTabs, NTabPane, NButton, NFormItemRow, NInput, NInputGroup},
  setup() {
    return {message: useNotification()}
  },
  data() {
    return {
      _tel: '',
      code: '',
      _password: '',
      confirmPassword: '',
      device: '',
      msg: '',
      time: 60,
      // userInfo: this.$yin.me.wx.userInfo || {},
      backTo: null,
      register: false
    }
  },
  // computed: {
  //   uninitialized() {
  //     return this.$yin.system.uninitialized
  //   }
  // },
  methods: {
    getCode() {
      this.$yin.User.auth.code.get(this._tel)
          .then(d => {
            this.device = d.device
            this.time = 60
            let timer = setInterval(() => {
              if (this.time) this.time--
              else clearInterval(timer)
            }, 1000)
          })
          .catch(msg => {
            this.msg = msg.message
          })
    },
    auth() {
      this.$yin.User.auth.code.auth(this)
          .then(user => {
            this.message.success({title: (user._title || user._tel) + ' 登录成功', duration: 2000})
            if (this.$route.name === 'auth') {
              if (this.$route.query.code) {
                const state = this.$route.query.state
                if (state) {
                  this.backTo = localStorage.getItem(state)
                  localStorage.removeItem(state)
                  this.back()
                }
                else {
                  this.backTo = '/'
                  this.back()
                }
              }
              else if (this.$route.query.url) {
                this.backTo = this.$route.query.url
                this.back()
              }
              else {
                // this.backTo = '/'
                this.back()
              }
            }
            else this.$emit('close')
          })
          .catch(err => {
            this.msg = err.response.data.msg || err.message
          })
    },
    authWX() {
      if (this.isWX && !this._store.state.user.wx.openid && !this.$route.query.code) {
        let url = this.$route.query.url, state = undefined
        if (url) {
          state = 'authBackUrl' + new Date().getTime()
          localStorage.setItem(state, url)
        }
        this.$yin.User.auth.wx.auth(state || '')
      }
      else if (this.$route.query.code) {
        this.$yin.User.auth.wx.getUserInfo(this.$route.query.code)
            .then(() => {
              const state = this.$route.query.state
              if (state) {
                this.backTo = localStorage.getItem(state)
                localStorage.removeItem(state)
                this.back()
              }
              else {
                this.backTo = '/'
                this.back()
              }
            })
            .catch(u => this.userInfo = u)
      }
    },
    async reg() {
      try {
        const user = await this.$yin.User.register(this._tel, this._password)
        this.message.success({title: (user._title || user._tel) + ' 注册成功', duration: 2000})
        if (this.$route.query.url) {
          this.backTo = this.$route.query.url
          this.back()
        }
        else {
          // this.backTo = '/'
          this.back()
        }
      }
      catch (err) {
        this.msg = err?.response?.data?.msg || err?.message
      }
    },
    async authPassword() {
      try {
        const user = await this.$yin.User.authPassword(this._tel, this._password)
        this.message.success({title: (user._title || user._tel) + ' 登录成功', duration: 2000})
        if (this.$route.query.url) {
          this.backTo = this.$route.query.url
          this.back()
        }
        else {
          // this.backTo = '/'
          this.back()
        }
      }
      catch (err) {
        this.msg = err?.response?.data?.msg || err?.message
      }
    },
    back() {
      console.log(this.backTo)
      if (this.backTo)
        this.$router.push(this.backTo)
      else
        this.$router.go(-1)
    },
    // waitForAuth() {
    //   console.log('waitForAuth')
    //   setTimeout(() => {
    //     if (this.$yin.me._id) this.back()
    //     else if (this.$yin.me.token) this.waitForAuth()
    //     else this.back()
    //   }, 100)
    // }
  },
  mounted() {
    this.authWX()
    if (this.$yin.me._id) this.back()
    else if (this.$yin.me._token) this.back()
    if (this.$yin.system.uninitialized)
      this.register = true
  }
})
</script>

<style scoped lang="less">
.auth {
  //background-color: rgba(var(--themeColorArray), 0.99);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .table {
    overflow: hidden;
    box-sizing: border-box;
    max-width: 500px;
    margin: 13px;
  }

  .title {
    margin: 0;
    opacity: .6;

    &:last-of-type {
      padding-bottom: 13px;
    }
  }

  .btn {
    width: 100%;
    padding-top: 13px;
    box-sizing: border-box;
  }

  .btn.raw {
    display: flex;

    button {
      width: 10%;
      flex-grow: 1;
      margin-right: 13px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  .authing {
    text-align: center;
  }
}
</style>
